<script>

export default {
    //
    data() {
		return {
            pageFlag : '',
			tableName: '',
			registerForm: {
                xingbie: '',
                xingbie: '',
            },
			rules: {},
            xueyuanxingbieOptions: [],
            jiaxiaojiaolianxingbieOptions: [],
		}
    },
	mounted() {
	},
    created() {
      this.pageFlag = this.$route.query.pageFlag;
      this.tableName = this.$route.query.role;
      if ('xueyuan' == this.tableName) {
        this.rules.xueyuanzhanghao = [{ required: true, message: '', trigger: 'blur' }];
      }
      if ('xueyuan' == this.tableName) {
        this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
      }
        this.xueyuanxingbieOptions = ",".split(',');
      if ('xueyuan' == this.tableName) {
        this.rules.shouji = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
      }
      if ('xueyuan' == this.tableName) {
        this.rules.shenfenzheng = [{ required: true, validator: this.$validate.isIdCard, trigger: 'blur' }];
      }
      if ('jiaxiaojiaolian' == this.tableName) {
        this.rules.jiaolianzhanghao = [{ required: true, message: '', trigger: 'blur' }];
      }
      if ('jiaxiaojiaolian' == this.tableName) {
        this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
      }
      if ('jiaxiaojiaolian' == this.tableName) {
        this.rules.nianling = [{ required: true, validator: this.$validate.isIntNumer, trigger: 'blur' }];
      }
        this.jiaxiaojiaolianxingbieOptions = ",".split(',');
      if ('jiaxiaojiaolian' == this.tableName) {
        this.rules.lianxidianhua = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
      }
    },
    //
    methods: {
      // uuid
      getUUID () {
        return new Date().getTime();
      },
        // 
      xueyuantouxiangUploadChange(fileUrls) {
          this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
      jiaxiaojiaoliantouxiangUploadChange(fileUrls) {
          this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },

        // 


      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var url=this.tableName+"/register";
               if(`xueyuan` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
                this.$message.error(``);
                return
               }
               if(`jiaxiaojiaolian` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
                this.$message.error(``);
                return
               }
            this.$http.post(url, this.registerForm).then(res => {
              if (res.data.code === 0) {
                this.$message({
                  message: '',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.$router.push('/login');
                  }
                });
              } else {
                this.$message.error(res.data.msg);
              }
            });
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

