	  border: 1px solid rgba(189, 125, 71, 1);
	  border-radius: 4px;
	  padding: 0 10px;
	  box-shadow: rgba(189, 125, 71, 1)rgba(189, 125, 71, 1);
	  outline: none;
	  color: #666;
	  width: 200px;
	  font-size: 14px;
	  height: 40px;
	}
	
	.add-update-preview .el-date-editor /deep/ .el-input__inner {
	  border: 1px solid rgba(189, 125, 71, 1);
	  border-radius: 4px;
