<template>
<div :style='{"padding":"20px 20px 40px","margin":"10px auto 0","borderColor":"rgba(189, 125, 71, 1)","borderRadius":"12px","background":"#fff","borderWidth":"3px","width":"1200px","position":"relative","borderStyle":"solid dashed double double"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="yuyuehao">
            <el-input v-model="ruleForm.yuyuehao" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="kaoshikemu">
            <el-input v-model="ruleForm.kaoshikemu" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="kaoshididian">
            <el-input v-model="ruleForm.kaoshididian" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="kaoshifeiyong">
            <el-input v-model="ruleForm.kaoshifeiyong" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="kaoshishijian">
            <el-input v-model="ruleForm.kaoshishijian" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="xueyuanzhanghao">
            <el-input v-model="ruleForm.xueyuanzhanghao" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="xueyuanxingming">
            <el-input v-model="ruleForm.xueyuanxingming" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="shenfenzheng">
            <el-input v-model="ruleForm.shenfenzheng" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="shouji">
            <el-input v-model="ruleForm.shouji" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="quxiaoyuanyin">
            <el-input v-model="ruleForm.quxiaoyuanyin" 
                placeholder="" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="dengjishijian" >
              <el-date-picker
                  value-format="yyyy-MM-dd HH:mm:ss"
                  v-model="ruleForm.dengjishijian" 
                  type="datetime"
                  placeholder="">
              </el-date-picker>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"4px","background":"radial-gradient(circle, rgba(189, 125, 71, 1) 0%, rgba(189, 125, 71, 1) 81%, rgba(189, 125, 71, 1) 100%)","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit"></el-button>
        <el-button :style='{"border":"1px solid rgba(189, 125, 71, 1)","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"rgba(189, 125, 71, 1)","borderRadius":"4px","background":"#ecfaea","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()"></el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            yuyuehao : false,
            kaoshikemu : false,
            kaoshididian : false,
            kaoshifeiyong : false,
            kaoshishijian : false,
            xueyuanzhanghao : false,
            xueyuanxingming : false,
            shenfenzheng : false,
            shouji : false,
            quxiaoyuanyin : false,
            dengjishijian : false,
            sfsh : false,
            shhf : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          yuyuehao: '',
          kaoshikemu: '',
          kaoshididian: '',
          kaoshifeiyong: '',
          kaoshishijian: '',
          xueyuanzhanghao: '',
          xueyuanxingming: '',
          shenfenzheng: '',
          shouji: '',
          quxiaoyuanyin: '',
          dengjishijian: '',
        },
        rules: {
          yuyuehao: [
          ],
          kaoshikemu: [
          ],
          kaoshididian: [
          ],
          kaoshifeiyong: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          kaoshishijian: [
          ],
          xueyuanzhanghao: [
          ],
          xueyuanxingming: [
          ],
          shenfenzheng: [
            { validator: this.$validate.isIdCard, trigger: 'blur' },
          ],
          shouji: [
            { validator: this.$validate.isMobile, trigger: 'blur' },
          ],
          quxiaoyuanyin: [
            { required: true, message: '', trigger: 'blur' },
          ],
          dengjishijian: [
          ],
          sfsh: [
          ],
          shhf: [
          ],
        },
      };
    },
    computed: {



    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.dengjishijian = this.getCurDateTime()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 
      download(file){
        window.open(`${file}`)
      },
      // 
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='yuyuehao'){
              this.ruleForm.yuyuehao = obj[o];
              this.ro.yuyuehao = true;
              continue;
            }
            if(o=='kaoshikemu'){
              this.ruleForm.kaoshikemu = obj[o];
              this.ro.kaoshikemu = true;
              continue;
            }
            if(o=='kaoshididian'){
              this.ruleForm.kaoshididian = obj[o];
              this.ro.kaoshididian = true;
              continue;
            }
            if(o=='kaoshifeiyong'){
              this.ruleForm.kaoshifeiyong = obj[o];
              this.ro.kaoshifeiyong = true;
              continue;
            }
            if(o=='kaoshishijian'){
              this.ruleForm.kaoshishijian = obj[o];
              this.ro.kaoshishijian = true;
              continue;
            }
            if(o=='xueyuanzhanghao'){
              this.ruleForm.xueyuanzhanghao = obj[o];
              this.ro.xueyuanzhanghao = true;
              continue;
            }
            if(o=='xueyuanxingming'){
              this.ruleForm.xueyuanxingming = obj[o];
              this.ro.xueyuanxingming = true;
              continue;
            }
            if(o=='shenfenzheng'){
              this.ruleForm.shenfenzheng = obj[o];
              this.ro.shenfenzheng = true;
              continue;
            }
            if(o=='shouji'){
              this.ruleForm.shouji = obj[o];
              this.ro.shouji = true;
              continue;
            }
            if(o=='quxiaoyuanyin'){
              this.ruleForm.quxiaoyuanyin = obj[o];
              this.ro.quxiaoyuanyin = true;
              continue;
            }
            if(o=='dengjishijian'){
              this.ruleForm.dengjishijian = obj[o];
              this.ro.dengjishijian = true;
              continue;
            }
          }
        }
        // 
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if(json.xueyuanzhanghao!=''&&json.xueyuanzhanghao){
                this.ruleForm.xueyuanzhanghao = json.xueyuanzhanghao
            }
            if(json.xueyuanxingming!=''&&json.xueyuanxingming){
                this.ruleForm.xueyuanxingming = json.xueyuanxingming
            }
            if(json.shenfenzheng!=''&&json.shenfenzheng){
                this.ruleForm.shenfenzheng = json.shenfenzheng
            }
            if(json.shouji!=''&&json.shouji){
                this.ruleForm.shouji = json.shouji
            }
          }
        });
      },

    // 
      // 
      info(id) {
        this.$http.get('quxiaokaoshi/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 
      onSubmit() {

        //
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('quxiaokaoshi/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 


                          this.$http.post('quxiaokaoshi/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post('quxiaokaoshi/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // uuid
      getUUID () {
        return new Date().getTime();
      },
      // 
      back() {
        this.$router.go(-1);
      },
    }
  };
</script>

