	  box-shadow: rgba(189, 125, 71, 1)rgba(189, 125, 71, 1);
	  color: rgba(189, 125, 71, 1);
	  width: 200px;
	  font-size: 32px;
	  line-height: 120px;
	  text-align: center;
	  height: auto;
	}
	
	.add-update-preview .el-textarea /deep/ .el-textarea__inner {
	  border: 1px solid rgba(189, 125, 71, 1);
